@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap');


.App {
  text-align: center;
}

#root{
  text-align: center;
  background:#f1f5f4;
  font-family: 'Noto Sans JP', sans-serif;
}


#select_prefecture{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-family: 'Noto Sans JP', sans-serif;
}


#detail_prefecture{
  margin:30px;

}

#calc_result_main{
  margin:0px;
  background: #96c31d;
}

.compare_result{
  margin:0px;
  background: #ccc;
}

.strong_result{
  font-weight:bold;
  font-size:150%;
  margin: auto .4em;
}

#share{
  margin:30px 30px 0px 30px;
}

.bottom_caution{
  font-size:50%;
  margin:20px;
}

h1{
  display: inline-block;
  padding:20px;
  margin:0px;
  color: #fff;
  background: #000;
}

h3{
  margin:0px 50px 50px 50px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
  font-family: 'Noto Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/notosans/v26/o-0IIpQlx3QUlC5A4PNr6DRAW_0.woff2) format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
